.map {

    &-container {
        padding: 74px 71px;
    }
    &__addres{
        display: flex;
        align-items: center;
        margin-bottom: 21px;

        &-title{
            font-weight: 700;
            font-size: 28px;
            line-height: 38px;
            display: flex;
            align-items: center;
            color: #314781;
        }
        &-icon{
            width: 26px;
            height: 31px;
            margin-right: 12px;
        }
    }
    &__descr{
        font-style: normal;
        font-weight: 700;
        font-size: 22px;
        line-height: 30px;
        color: #33BAEC;
    }
    &__google{
        margin-bottom: 40px;
    }
}