.general {
    
    
    &-container{
        padding: 0 60px;
        position: relative;
    }
    &__content {
        padding-bottom: 47px;
        display: flex;
        justify-content: space-between;
    }
    &__title{
        padding: 36px 0 30px;
        font-style: normal;
        font-weight: 700;
        font-size: 28px;
        line-height: 38px;
        color: #33BAEC;
    }
    &__img {
        background: url(./../../img/ship.png) no-repeat center;
        width: 991px;
        height: 577px;  
    }
    &__items{
        // margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        
    }
    &__item {
        
        &__num {
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #33BAEC;
            margin-bottom: 7px;
        }
        &__descr {
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 19px;
            text-transform: uppercase;
        }
    }
}
.general__bg {
    display: none;
    position: absolute;
    // background: url(./../../img/anchor.png) no-repeat;
    width: 584px;
    height: 584px;
    top: 100px;
    right: 0;
    transform: translate(50%);
}