
.vacancy-page {
    background-color: #fff;
}
.vacancy{
    &-container{
        padding: 41px 65px 184px;
    }
    &__content{
    }
    &__items{
        
        display: flex;
        flex-direction: column;
        gap: 26px;
    }
    &__item {
        padding: 40px 60px;
        background: #F9F8F8;
    }
    &__title{
        
        font-style: normal;
        font-weight: 700;
        font-size: 28px;
        line-height: 38px;
        display: flex;
        color: #33BAEC;
        margin-bottom: 32px;
    }
    &__item__card{

        &-title{
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #314781;
        }
        &-list{

        }
        &:not(:last-child){
            margin-bottom: 30px;
        }
    }
    &-list__item {
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 25px;
    }
}
