.terminals {
    padding-bottom: 105px;

    &-container {

    }
    &__items {
        display: flex;
        justify-content: center;
    }
    &__item {
        padding: 0 30px;
        width: 33%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        max-height: 365px;

        &__title{
            font-style: normal;
            font-weight: 700;
            font-size: 28px;
            line-height: 38px;
            text-align: center;
            color: #33BAEC;
            margin-bottom: 34px;
        }
        &__img{
            margin-bottom: 34px;
        }
        &__readmore{
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #33BAEC;
        }
    }
    &__item + &__item {
        border-left: 1px solid #000;
    }
}