.sidebar {
    background:  #F9F8F8;
    max-width: 375px;
    width: 100%;
    &-container{

    }
    &__list{
        padding: 86px 0;
        text-align: center;
    }
    &__item{

        &:not(:last-child){
            margin-bottom: 22px;
        }
    }
    &__link{
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;
        color: #20305B;
        &.active {
            font-weight: 900;
            color: #33BAEC;
        }
    }
}