.managements-page {
    background: #ffffff;
}

.managements {
    
   
    
    &-container {
        padding: 77px 70px;
    }
        

    &__items {    
        
        display: grid;
        grid-template-columns: repeat(3,1fr);
        grid-gap: 50px;
        
    }

    &__item {
        font-style: normal;
        font-weight: 800;
        font-size: 56px;
        line-height: 66px;
        color: #ffffff;

        display: flex;
        flex-direction: column;
        row-gap: 10px;

        max-width: 286px;
        max-height: 414px;

        &-img{
            border-radius: 50%;
        }
        &-title{
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            text-align: center;
            color: #314781;  
        }
        &-prof{
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 25px;
            text-align: center;
            color: #000000; 
        }
        &-mail,&-phone{
            display: flex;
            align-items: center;
        }
        &-mail {
            margin-bottom: 6px;
        }
        &-contacts{
            margin: 0 15px;
        }
    }
    .phone{
        &-img{
            margin-right: 8px;
        }
        &-text{
            font-style: normal;
font-weight: 300;
font-size: 17px;
line-height: 23px;
display: flex;
align-items: center;
text-indent: 1px;

color: #000000;
        }
    }
    .mail{
        &-img{
            margin-right: 8px;
        }
        &-text{
            font-style: normal;
font-weight: 300;
font-size: 17px;
line-height: 23px;
display: flex;
align-items: center;
text-indent: 1px;

color: #000000;
        }
    }
}

