.infrastructure {



    &-container {
        padding: 0 60px;
        position: relative;
    }
    &__content{
        display: flex;
        justify-content: space-between;
        margin-bottom: 70px;
    }
    &__items {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__item {


        &__num{
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #33BAEC;
            margin-bottom: 7px;
        }
        &__descr{
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 19px;
            text-transform: uppercase;
        }
    }
    &__text {
        max-width: 966px;
        &__title {
            font-style: normal;
            font-weight: 700;
            font-size: 28px;
            line-height: 38px;
            color: #33BAEC;
            margin-bottom: 21px;
        }
        &__1 {


            &-descr1{
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 25px;
                letter-spacing: 0.01px;
                margin-bottom: 55px;
            }
        }
        
    }
    &__list {

        padding-left: 20px;
        &__item {
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 25px;
            letter-spacing: 0.01px;
            &:not(:last-child){
                margin-bottom: 20px;
            }
        }
    }
}
.infrastructure__bg {
    display: none;
    position: absolute;
    // background: url(./../../img/anchor-left.png) no-repeat;
    width: 584px;
    height: 584px;
    top: 0;
    left: 0;
    transform: translate(50%);
}