.plug-page {
    background: #ffffff;
}

.plug {
    

    &-container{
        display: block !important;
        padding: 44px 70px 191px;
    }
    &__items{
        display: flex;
        flex-direction: column;
        row-gap: 20px;
    }
    &__item{
        
        background: #F9F8F8;
        &-left{
            display: flex;
            justify-content: center;
            flex: 1;
            align-items: center;
            flex-direction: column;

            &__img{

            }
        }

        &__content {
            display: flex;
            padding: 42px 0 23px;
        }
        
        &-right{
            display: flex;
            gap: 20px;
            flex-wrap: wrap;
            flex: 1;
            &__img{

            }
        }

        &__title{
            font-style: normal;
            font-weight: 700;
            font-size: 28px;
            line-height: 38px;
            text-align: center;
            color: #33BAEC;
        }
    }
    
}